html {
  /*fontstack with fallback*/
  font-family: -apple-system, BlinkMacSystemFont, ".SFNSDisplay-Regular", ".SFNSText-Regular", "SF UI Text", "San Francisco", "Noto Sans", "Helvetica Neue", "Roboto", "Droid Sans", "Oxygen", "Lucida Grande", "Segoe UI", "Fira Sans", Ubuntu, Cantarell, Helvetica, Arial, sans-serif;
  /*
  font-size: 100%; (1em ~= 16px ~= 14pt)
  font-size: 62.5%; (1em = 10px)
  */
}

body {
  /*font rendering*/
  /*-webkit-font-smoothing: antialiased;*/
  /*text-rendering: optimizeLegibility;
  text-rendering: optimizeSpeed;*/
}


/*utility*/

.float-center {
  display: block;
  margin-right: auto;
  margin-left: auto;
}


/*clearfix*/

.cf::after {
  content: "";
  display: block;
  clear: both;
}


/*hyphens !require lang defined*/

p {
  -webkit-hyphens: auto;
  -moz-hyphens: auto;
  hyphens: auto;
  -epub-hyphens: auto;
  -ms-hyphens: auto;
}


/*no select*/

.noselect {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-touch-callout: none;
}


/*dealing with long placeholders*/

input[placeholder] {
  text-overflow: ellipsis;
}

input::-moz-placeholder {
  text-overflow: ellipsis;
}

input:-moz-placeholder {
  text-overflow: ellipsis;
}

input:-ms-input-placeholder {
  text-overflow: ellipsis;
}


/*pointer and noselect*/

label {
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}


/*to escape some problems*/

textarea {
  resize: vertical;
}


/*this hack vs display: block;*/

img {
  vertical-align: middle;
}


/*adaptive video*/

.video {
  position: relative;
  overflow: hidden;
  padding-bottom: 56.25%;
  height: 0;
}

.video iframe,
.video object,
.video embed {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}


/*buttons pointer*/

button,
[role="button"] {
  cursor: pointer;
}


/*IE 10 support for HTML5 hidden attr*/

[hidden] {
  display: none !important;
}


/*box fix*/

*,
*::before,
*::after,
.box-fix {
  box-sizing: border-box;
}


